import { NavConfig } from "@config/navigation/config"
import { Redirect } from "@components/Redirect"
import { SampleContainer } from "@views/Dashboard/module/sections/Sample"
import { lazy, Suspense } from "react"
import { IndexRouteObject, NonIndexRouteObject, Outlet } from "react-router"

type IIndexRouteObject = Omit<NonIndexRouteObject, "children"> & {
  children?: IRouteObject[]
}
type IRouteObject = IndexRouteObject | IIndexRouteObject

const AboutUs = lazy(() => import("@views/OurOrganization"))
const Dashboard = lazy(() => import("@views/Dashboard"))
const DivineAndDirective = lazy(() => import("@views/DivineAndDirective"))
const Insignias = lazy(() => import("@views/Insignias"))
const OurWingsAndActivities = lazy(
  () => import("@views/OurWingsAndActivities/module/Container")
)
const UnitsPage = lazy(() => import("@views/UnitsPage"))

const LazyElementsForOurOrganization: { [key: string]: React.ReactNode } =
  Object.freeze({
    "the-organization-about-us": <AboutUs />,
    "the-organization-wings-activities": <OurWingsAndActivities />,
    "the-organization-divine-directives-guidelines": <DivineAndDirective />,
    "the-organization-insignias": <Insignias />,
    "the-organization-units": <UnitsPage />
  })

export const routes: IRouteObject[] = [
  {
    id: "base-ui",
    path: "/",
    element: (
      <Suspense fallback={<div>Loading...</div>}>
        <Outlet />
      </Suspense>
    ),
    children: [
      {
        id: "redirect-home",
        index: true,
        element: <Redirect path="/home" key={"home-redirect"} />
      },
      {
        id: NavConfig["home"].id,
        path: NavConfig["home"].path,
        element: <Dashboard />
      },
      {
        id: NavConfig["important-initiatives-and-programs"].id,
        path: NavConfig["important-initiatives-and-programs"].path,
        element: <SampleContainer />
      },
      {
        id: NavConfig["the-organization"].id,
        path: NavConfig["the-organization"].path,
        children: NavConfig["the-organization"].children?.map(
          (item, index) => ({
            id: item.id,
            path: item.path,
            index: index === 0,
            element: LazyElementsForOurOrganization?.[item.id]
          })
        )
      },
      {
        id: NavConfig["resources"].id,
        path: NavConfig["resources"].path,
        element: <SampleContainer />
      },
      {
        id: "not-found-base",
        path: "*",
        element: <>Not Found</>
      }
    ]
  },
  {
    id: "not-found-all",
    path: "*",
    element: <>Not Found</>
  }
]
